import { createStore } from "vuex";

export default createStore({
  state: {
    count: 1,
  },
  getters: {},
  mutations: {
    addx(state, v) {
      setTimeout(() => {
        state.count += v;
      }, 5000);
    },
  },
  actions: {
    addxx(state, v) {
      setTimeout(() => {
        state.state.count += v;
      }, 5000);
    },
  },
  modules: {},
});
