import {
  createRouter,
  createWebHashHistory,
  createWebHistory,
} from "vue-router";

//路由数组
const routes = [
  {
    //基本格式
    path: "/login",
    name: "Login",
    component: () => import("../views/Login.vue"),
    children: [],
  },
  {
    path: "/",
    name: "HelloWorld",
    component: () => import("../components/HelloWorld.vue"),
    children: [],
  },
];

//路由对象
//导出路由对象，在main.js中引用
export default createRouter({
  history: createWebHistory(),
  routes, //上面的路由数组
});
